.Main_container {
  height: 4000px;
}
.Main_container .Main_bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-image: url("../img/Main_img.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.Main_container .Main_content {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 1000px;
  height: 600px;
  perspective: 1000px;
}
.Main_container .Main_content .boxs {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 1;
  transition: all 0.2s;
}
.Main_container .Main_content .boxs .title {
  width: 100%;
  font-size: 40px;
  font-family: "twaysky";
  padding: 15px;
}
.Main_container .Main_content .boxs.on {
  z-index: 11;
  opacity: 0;
}
.Main_container .Main_content .boxs:nth-child(1) {
  transform: translateZ(0px);
  z-index: 20;
}
.Main_container .Main_content .boxs:nth-child(2) {
  transform: translateZ(-1000px);
  z-index: 19;
}
.Main_container .Main_content .boxs:nth-child(3) {
  transform: translateZ(-2000px);
  z-index: 18;
}
.Main_container .Main_content .boxs:nth-child(4) {
  transform: translateZ(-3000px);
  z-index: 17;
}
.Main_container .Main_content .boxs.main {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  position: relative;
}
.Main_container .Main_content .boxs.main::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  border: 1px solid #4e4e4e;
  transform: rotateZ(4deg);
}
.Main_container .Main_content .boxs.main h1 {
  color: #fff;
  letter-spacing: 3px;
  font-family: "twaysky";
  font-size: 150px;
  font-weight: 600;
}
.Main_container .Main_content .boxs.main h1 span {
  color: #fff;
  text-shadow: -1px -1px 0 #ccc, 1px -1px 0 #ccc, -1px 1px 0 #ccc, 1px 1px 0 #ccc;
}

.About {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #FFF;
}
.About::after {
  z-index: 1;
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  border: 1px solid #4e4e4e;
  transform: rotateZ(2deg);
}
.About .About_menu {
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.About .About_menu div {
  margin: 10px;
  padding: 10px;
}
.About .About_menu div span {
  font-family: "twaysky";
  font-size: 50px;
  font-weight: 600;
  color: #fff;
  transition: all 0.3s;
  text-shadow: -1px -1px 0 #979797, 1px -1px 0 #979797, -1px 1px 0 #979797, 1px 1px 0 #979797;
}
.About .About_menu div p {
  font-size: 25px;
  font-weight: 600;
  font-family: "Pretendard-Regular";
}
.About .About_menu div ul {
  margin-top: 10px;
  font-family: "Pretendard-Regular";
}
.About .About_menu div ul li {
  font-size: 12px;
  letter-spacing: 1px;
  margin-bottom: 5px;
}
.About .About_menu div ul li a {
  color: #000;
}
.About .About_menu div ul li a:hover {
  color: rgb(0, 119, 255);
}
.About .About_menu div:hover span {
  font-family: "twaysky";
  font-size: 50px;
  font-weight: 600;
  color: black;
  text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.About .About_me {
  width: 100%;
  font-size: 14px;
  display: flex;
}
.About .About_me .AboutMe_area {
  width: 50%;
  font-family: "Pretendard-Regular";
  margin-left: 30px;
  margin-top: 30px;
}
.About .About_me .AboutMe_area ul {
  display: flex;
}
.About .About_me .AboutMe_area ul li {
  font-size: 25px;
  list-style: none;
}
.About .About_me .AboutMe_area ul li span {
  font-weight: 600;
  margin-right: 10px;
}
.About .About_me .AboutMe_area p {
  margin-top: 10px;
  width: 500px;
  line-height: 25px;
}
.About .About_me .AboutMe_skils {
  width: 45%;
}
.About .About_me .AboutMe_skils .satellites {
  animation: rotate-axis 10s linear infinite;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
}
@keyframes rotate-axis {
  from {
    transform: perspective(800px) rotateY(0deg);
  }
  to {
    transform: perspective(800px) rotateY(360deg);
  }
}
.About .About_me .AboutMe_skils .satellites .satellite {
  position: absolute;
  width: 200px;
  transform: rotateY(calc(var(--i) * 1deg)) translateZ(100px);
  color: #979797;
  font-size: 20px;
  font-family: "twaysky";
  transition: all 0.3s;
}
.About .About_me .AboutMe_skils .satellites .satellite:hover {
  color: rgb(0, 119, 255);
}
.About .About_me .AboutMe_skils .satellites .satellite:nth-child(1) {
  transform: rotateY(0deg) translateZ(150px);
}
.About .About_me .AboutMe_skils .satellites .satellite:nth-child(2) {
  transform: rotateY(100deg) translateZ(150px);
}
.About .About_me .AboutMe_skils .satellites .satellite:nth-child(3) {
  transform: rotateY(200deg) translateZ(150px);
}
.About .About_me .AboutMe_skils .satellites .satellite:nth-child(4) {
  transform: rotateY(300deg) translateZ(150px);
}
.About .About_me .AboutMe_skils .satellites.sub {
  margin-top: 50px;
}
.About .About_me .AboutMe_skils .AboutMe_list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 100px;
}
.About .About_me .AboutMe_skils .AboutMe_list div {
  width: 100%;
  margin-bottom: 20px;
}
.About .About_me .AboutMe_skils .AboutMe_list div p {
  margin-right: 30px;
  font-size: 25px;
  font-family: "twaysky";
}
.About .About_me .AboutMe_skils .AboutMe_list div ul {
  margin-top: 10px;
}
.About .About_me .AboutMe_skils .AboutMe_list div ul li {
  margin: 5px 0;
  font-family: "Pretendard-Regular";
}

.Work {
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #FFF;
}
.Work::after {
  z-index: -10;
  position: absolute;
  top: 0;
  content: "";
  width: 100%;
  height: 100%;
  border: 1px solid #4e4e4e;
  background-color: #616161;
  transform: rotateZ(2deg);
}
.Work .work_plus {
  width: 100%;
  text-align: center;
  margin-top: 50px;
  line-height: 20px;
}
.Work .work_plus a {
  display: block;
  margin-top: 30px;
  color: #444444;
  transition: all 0.3s;
}
.Work .work_plus a:hover {
  color: blue;
  transition: all 0.3s;
}
.Work .Work_list {
  font-family: "Pretendard-Regular";
  display: flex;
  flex-wrap: wrap;
}
.Work .Work_list .Work_item {
  width: 23%;
  height: 190px;
  border: 1px solid #ccc;
  border-radius: 15px;
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
  overflow: hidden;
  margin: 5px 10px;
}
.Work .Work_list .Work_item p {
  transition: all 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 1px;
  width: 100%;
  text-align: center;
}
.Work .Work_list .Work_item p span {
  font-size: 12px;
}
.Work .Work_list .Work_item:hover::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.Work .Work_list .Work_item:hover p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  color: #fff;
}
.Work .Work_list .Work_item:nth-child(1) {
  background: url("../img/sbox.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(2) {
  background: url("../img/otua.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(3) {
  background: url("../img/raon.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(4) {
  background: url("../img/otuastudy.png") no-repeat;
  background-size: cover;
}

