.modal__wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 98;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-flow: wrap;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease;
  overflow: hidden;
}
@media (min-width: 48rem) {
  .modal__wrap {
    align-items: center;
  }
}
.modal__wrap.is-open {
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease;
}
.modal__wrap.is-open .modal__card {
  transform: translate3d(0, 0, 0);
  transition: all 0.5s cubic-bezier(0.4, 0, 1, 1);
}
@media (min-width: 48rem) {
  .modal__wrap.is-open .modal__card {
    transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  }
}
.modal__wrap .modal__card {
  font-family: "Pretendard-Regular";
  padding: 20px;
  height: 400px;
  display: block;
  overflow-y: scroll;
  background: #ffffff;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.15), 0 0 1px 0 rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  transform: translate3d(0, 100%, 0);
  transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  position: relative;
}
@media (min-width: 48rem) {
  .modal__wrap .modal__card {
    transform: translate3d(0, -25%, 0);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
}
.modal__wrap .modal__card header {
  min-width: 750px;
  display: flex;
}
.modal__wrap .modal__card header div {
  width: 100%;
}
.modal__wrap .modal__card span {
  padding-top: 10px;
  display: block;
  font-size: 14px;
  color: #979797;
  font-weight: 500;
}
.modal__wrap .modal__card p {
  margin-top: 5px;
}
.modal__wrap .modal__card .prev_btn {
  position: absolute;
  right: 30px;
  top: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  text-align: center;
  line-height: 30px;
  color: #444444;
  font-weight: 600;
}
.modal__wrap .modal__card img {
  margin-top: 10px;
  height: 600px;
  width: 100%;
  overflow-y: auto;
}
.modal__wrap .modal__card .Work_viewBtns {
  width: 100%;
  margin-top: 50px;
  background-color: #fff;
  border: none;
}
.modal__wrap .modal__card .Work_viewBtns a {
  background-color: #fff;
  transition: all 0.3s;
  padding: 5px 40px;
  border: 1px solid #000;
  font-size: 17px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.modal__wrap .modal__card .Work_viewBtns a:hover {
  background-color: #000;
  color: #fff;
}

