.Header {
  z-index: 99;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  height: 100px;
  align-items: center;
  background-color: transparent;
  transition: all 0.5s;
  padding: 10px 0;
}
.Header .gnb {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
}
.Header .gnb li {
  margin: 0 20px;
}
.Header .gnb li a {
  color: #000;
  font-size: 15px;
  font-weight: 600;
}